import * as React from 'react';
import "./SelectedNodeList.css";
import { Table, Pagination, Tag, Button, Select, Switch} from 'antd';
import {getLayoutMode, compareSelectedNodeIdList, getNodeStatisticStr} from '../../../helper';
import ProjectionView from './ProjectionView';
const d3 = require("d3");
export interface SelectedNodeListProps {
    refreshnumber:number,
    filters:any,
    PCPJson:any,
    changeSelectedNodeIdList: any,
    width:number,
    height: number,
    showSource:boolean,
    changeShowSource:any,
    changePSSelectedNodes:any
}

export interface SelectedNodeListState {
    currentPage:number,
    highlightNodeIdList:any,
    enableLines:boolean

}

export default class SelectedNodeList extends React.Component<SelectedNodeListProps, SelectedNodeListState> {
    public prevSelectedNodeIdList:any[] = [];
    public thisSelectedNodeIdList:any[] = [];
    constructor(props: SelectedNodeListProps) {
        super(props);
        this.onChangeHighLightNodeIdList = this.onChangeHighLightNodeIdList.bind(this);
        this.renderLines = this.renderLines.bind(this);
        this.onChangeLines = this.onChangeLines.bind(this);
        //this.mappingGraph = this.mappingGraph.bind(this);
        //this.onSelectedNodeListChange = this.onSelectedNodeListChange.bind(this);
        //this.onRowClick = this.onRowClick.bind(this);
        this.state = {
            //graph_data : []
            currentPage: 1,
            highlightNodeIdList: [],
            enableLines : true
        };
    }
    componentDidMount(){
        
    }
    componentDidUpdate(prevProps:SelectedNodeListProps, prevState:SelectedNodeListState){
        if(!compareSelectedNodeIdList(this.prevSelectedNodeIdList,this.thisSelectedNodeIdList)){
            this.props.changeSelectedNodeIdList(this.thisSelectedNodeIdList)
            this.prevSelectedNodeIdList = this.thisSelectedNodeIdList.slice();
            this.setState({
                highlightNodeIdList: []
            })
        }
        if(this.state.enableLines){
            this.renderLines();
        }
    }

    
    
    public constructSelectedNodeList(filters:any, PCPJson:any){
        if(getLayoutMode() === 3){
            let data = PCPJson.PSData.slice();
            let filterData:any[] = [];
            
            let filters_key = Object.keys(filters);
            const selected = (d:any)=>{
                if(filters_key.length>0){
                    for(let i = 0; i< filters_key.length;i ++){
                        if(filters[filters_key[i]].indexOf(d[filters_key[i]])<0){
                            return false;
                        }
                    }
                    return true;
                }else{
                    return true;
                }
            }
            data.forEach((d:any,index:number)=>{
                if(selected(d)){
                    let dataInstance = d;
                    filterData.push(dataInstance);
                }
            })
            return filterData;
        }else{
            let data = PCPJson["PCPData"].slice();
            let data_index = PCPJson["PCPIndex"].slice();
            const features = PCPJson["PCPDimension"];
            let typedict:any = {};
            features.forEach((d:any)=>{
                typedict[d.name] = d.type;
            })
            let filters_key = Object.keys(filters);
            const selected = (d:any)=>{
                if(filters_key.length>0){
                    for(let i = 0; i< filters_key.length;i ++){
                        if(typedict[filters_key[i]]==="continuous" || typedict[filters_key[i]] === "log"){
                            if( parseFloat(filters[filters_key[i]][1]) <= parseFloat(d[filters_key[i]]) 
                            && parseFloat(d[filters_key[i]])  <= parseFloat(filters[filters_key[i]][0])){

                            }else{
                                return false;
                            }
                        }else if(typedict[filters_key[i]] === "ordinal"){
                            if(filters[filters_key[i]].indexOf(d[filters_key[i]])<0){
                                return false;
                            }
                        }else{
                            console.log("unexpected type:", typedict[filters_key[i]]);
                        }
                        
                    }
                    return true;
                }else{
                    return true;
                }
            }
            let filterData:any[] = [];
            data.forEach((d:any,index:number)=>{
                if(selected(d)){
                    let dataInstance = d;
                    dataInstance.data_id = data_index[index];
                    filterData.push(dataInstance);
                }
            })
            
            return filterData;
        }
    }
    public constructDataJson(columns:any,selectedNode:any){
        let data_json:any = {};
        columns.forEach((d:any)=>{
            let sdata = selectedNode[d.dataIndex];
            if(d.dataIndex === "one_hop_accuracy"){
               sdata = sdata.toFixed(4);
            }
            data_json[d.title] = sdata;
        })
        return data_json;
    }
    public renderLines(){
        if(this.state.enableLines){
            let highlightNodeIdList = this.state.highlightNodeIdList;
            let all_data = [];
    
            for(let id = 1; id<=4; id++){
                var top_svg = d3.select("#"+"ScatterPlot_sub_"+id);
                var svg = top_svg.select("g");
                var nodes = svg.selectAll("g.nodes");
                var point_array_data:any = {};
                nodes.data().forEach((d:any)=>{
                    if(highlightNodeIdList.indexOf(d.data.Data_id)>=0){
                        point_array_data[d.data.Data_id] = {
                            "x":d.x,
                            "y":d.y
                        }
                    }else{
                    }
                })
                all_data.push({
                    "id":id,
                    "point_array":point_array_data
                });
            }
            let data_list = [];
            let marginLeft = 20, marginRight = 20;
            let marginTop = 20, marginBottom = 62;
            let projectionHeight = this.props.height - marginBottom - marginTop;
            let gap = 10;
            let projectionWidth = (this.props.width - marginLeft - marginRight) / 4  - gap;
            let successflag = true;
            for(let i = 0; i<highlightNodeIdList.length; i++){
                let node_id = highlightNodeIdList[i];
                let coords = [];
                for(let j = 0 ; j<all_data.length; j++){
                    if(all_data[j]["point_array"][node_id]){
                        let startX = marginLeft + (j)*(projectionWidth+gap);
                        let startY = marginTop;
                        let new_x = all_data[j]["point_array"][node_id]["x"] + startX;
                        let new_y = all_data[j]["point_array"][node_id]["y"] + startY;
                        //if(j>0){
                        //    let inner_x = startX - gap / 2;
                        //    let inner_y = new_y;
                        //    let inner_coords = [inner_x, inner_y];
                        //    coords.push(inner_coords);
                        //}
                        let new_coords = [new_x, new_y];
                        coords.push(new_coords);
                    }else{
                        successflag = false;
                        break;
                    }
                    
                }
                if(!successflag){
                    break;
                }
                data_list.push({
                    "node_id":node_id,
                    "coords": coords
                })
            }
            if(!successflag){
                data_list = [];
            }
            //console.log("data list", data_list);
            var connectionPath = d3.select("#connectionPath");
            if(connectionPath){
                var lines = connectionPath.selectAll("path")
                .data(data_list, function(d:any){
                    return d.node_id;
                })
                //console.log("line enter, update, exit", lines.enter(), lines, lines.exit());
                //var lineGenerator = d3.line().curve(d3.curveMonotoneY)
                var lineGenerator = d3.line().curve(d3.curveNatural);
                let constructPointStr = (d:any)=>{
                    /*let str = "";
                    for(let i = 0 ; i<d.coords.length; i++){
                        let point = d.coords[i];
                        str = str + point[0]+","+point[1]+" ";
                    }
                    return str;*/
                    return lineGenerator(d.coords.slice());
                }
               
                var line_enter = lines.enter().append("path");
                var line_enter_update = line_enter.merge(lines);
                lines.exit().remove();
                line_enter_update.attr("d", constructPointStr)
                .style("fill","none")
                .style("stroke","#999")
                .style("stroke-width",1.5)
                .style("opacity", 0.5)
                ;
            }
           
        }
        
    }
    public onChangeHighLightNodeIdList(selectedNodeIdList:any, highlightNodeIdList:any){
        this.props.changeSelectedNodeIdList(selectedNodeIdList.slice());
        this.setState({
            highlightNodeIdList: highlightNodeIdList.slice()
        })
        this.renderLines();
    }
    public onChangeLines(e:any){
        this.setState({
            enableLines : e
        })
    }
    public render() {
        let {filters,PCPJson,changeSelectedNodeIdList,showSource } = this.props;
        //console.log("Selected Node List", filters, PCPJson);
        let selectedNodeIdList:any, selectedNodeList:any ;
        if(PCPJson["success"]){
            selectedNodeList = this.constructSelectedNodeList(filters, PCPJson);
            
            
            selectedNodeIdList = selectedNodeList.map((d:any)=>{
                return d['Data_id'];
            })
        }else{
            selectedNodeList = [];
            selectedNodeIdList = [];
        }
        
        this.thisSelectedNodeIdList = selectedNodeIdList;
        
        let marginLeft = 20, marginRight = 20;
        let marginTop = 20, marginBottom = 62;
        let projectionHeight = this.props.height - marginBottom - marginTop;
        let gap = 10;
        let projectionWidth = (this.props.width - marginLeft - marginRight) / 4  - gap;
        this.props.changePSSelectedNodes(selectedNodeList.length);
        //changeSelectedNodeIdList(selectedNodeIdList);
        let id_list = [1,2,3,4];
        let showModeOptions = [
            [1, "GT + 3PT"],
            [2, "DEG + CN"],
            [3, "P1 + SPD"],
            [4, "P1 + KFS"],
        ]
        let onChangeLines = this.onChangeLines;
        if(selectedNodeIdList.length>0&&selectedNodeIdList.length<300){
        return (
            <div style={{width: "100%", height:""+(this.props.height - 10)+"px", overflowX: "scroll"}}>
            
            
                <div className="ViewTitle">Projection View
                <div style={{float:'right'}}>
                    Links: <Switch checked={this.state.enableLines} onChange={onChangeLines} />
                    &nbsp;&nbsp;&nbsp;&nbsp;
                     #Nodes: <Tag>{getNodeStatisticStr(this.state.highlightNodeIdList.length, selectedNodeIdList.length)}</Tag></div>
                                
                </div>
                <div className="ViewBox"> 
                <svg
                    style={{ height: ""+(this.props.height - marginBottom + 10)+"px", width: ""+this.props.width+"px" }}
                    id={"TopSVGChart_ScatterPlot"}
                    xmlns="http://www.w3.org/2000/svg"
                    >
                        {this.state.enableLines?(<g key={"c"+1} id="connectionPath"></g>):(<g key={"c"+2} id="connectionPath-Null"></g>)}
                        {showModeOptions.map((d:any)=>{
                            return    <ProjectionView 
                            key={d[0]}
                            id={d[0]} showMode={d[0]} 
                             {...this.props} 
                             x={marginLeft + (d[0]-1)*(projectionWidth+gap)}
                             y={marginTop}
                             height={projectionHeight} 
                             width={projectionWidth} 
                             name={d[1]}
                             selectedNodeIdList={selectedNodeIdList}
                             highlightNodeIdList={this.state.highlightNodeIdList}
                             onChangeHighLightNodeIdList={this.onChangeHighLightNodeIdList}
                             renderLines={this.renderLines}/>
                        })}
                        
                </svg>
                </div>
                
            </div>
        )
        }else{
            return <div style={{width: "100%", height:""+(this.props.height - 10)+"px", overflowX: "hidden"}}>
            
            
            <div className="ViewTitle">Projection View </div>
            
            </div>
        }
        
    }
}
