const Option = Select.Option;
//const d3 = require("d3");
export interface IProps {
    graph_object:any,
    show_mode:number, 
    explained_node:number, 
    onExplainNodeChange:any, 
    onShowModeChange:any,
    model:number,
    modelList:any[],
    width:number,
    height:number,
    specificNodeIdList: any[],
    select_inspect_node:number,
    showSource : boolean,
    extendedMode: any

}
export interface IState {
    axis_select : number,
    distance_select : number,
    dataSource_select: number,
    color_encode: number,
    enableSorting: boolean
}

export default class FeatureMatrixView extends React.Component<IProps, IState>{

    constructor(props:IProps) {
        super(props);
        this.onAxisChange = this.onAxisChange.bind(this);
        this.onDistanceChange = this.onDistanceChange.bind(this);
        this.onDataSourceChange = this.onDataSourceChange.bind(this);
        this.onColorEncodeChange = this.onColorEncodeChange.bind(this);
        this.onEnableSort = this.onEnableSort.bind(this);
        this.state = {
            axis_select : 2,
            distance_select: 1,
            dataSource_select : 1,
            color_encode: 2,
            enableSorting: true
        }
        //this.resize.bind(this);
        // Flow:
        // 1. Constructor
        // 2. componentWillMount()
        // 3. render()
        // 4. componentDidMount()
        // If props update:
        // 4.1 componentWillReceiveProps(nextProps : IProps), then goto 5.
        // If States update
        // 5. shouldComponentUpdate() if return false, then no rerendering.
        // 6. if True, then componentWillUpdate
        // 7. render()
        // 8. componentDidUpdate
        // If Unmount, then componentWillUnmount()
    }
    // Component Configurable Methods.
    componentDidMount(){
    }
    // Mount
    componentWillMount() {
        //console.log('Component will mount!')
     }

     
    // Update
     shouldComponentUpdate(nextProps:IProps, nextState:IState){
        return true;
    }
     componentWillUpdate(nextProps:IProps, nextState:IState) {
        //console.log('Component will update!');
     }

     componentDidUpdate(prevProps:IProps, prevState:IState) {

        
     }
     public constructFeatureMatrixJson(graph_object:any, selectedNodeIdList:any[], axis_select:number, 
        distance_select:number, dataSource_select:number, color_encode:number, select_inspect_node:number, 
        showSource:boolean, enableSorting:boolean, width:number, height:number, extendedMode:any){

        // axis_select : 2, Y Axis
        // distance_select: 1, Detailed Feature
        // dataSource_select : 1, Input Layer
        // color_encode: 2, Ground Truth
        /**
         * let dataSourceOptions = [
                [1, "Input Layer"],
                [2, "Hidden Layer"],
                [3, "Output Layer"]
            ];
            let distanceOptions = [
                [1, "Detailed Feature"],
                [2, "Distance"]
            ];

            let axisOptions = [
                [1, "X Axis"],
                [2, "Y Axis"]
            ];
            let colorOptions = [
                [2, "Ground Truth"],
                [3, "Model Output"],
                [5, "True / False"]
            ]
         */
        let selectedStr = selectedNodeIdList.join("_");
        let common = graph_object;
        let individual = graph_object;
        let graph_name; 
        let layout_mode = getLayoutMode();
        if(layout_mode === 3){
            common = graph_object.common;
            individual = graph_object.individual;
            //graph_name = common.name+"_"+common.dataset_id+"_"+(show_mode)+"_"+common.data_type_id+"_SELECTED_"+selectStr+"_SELECTEDEND_"+enableForceDirected+"_"+width+"_"+height+"_"+embedding_layout_select+"_";
            graph_name = common.name+"_"+common.dataset_id+"_"+common.data_type_id+"_SELECTED_"+selectedStr+"_SELECTED_END_"
            +axis_select+"_"+distance_select+"_"+dataSource_select+"_"+color_encode+"_"+select_inspect_node+"_"+enableSorting+"_"+width+"_"+height;
            if(showSource){
                graph_name = graph_name+"_"+extendedMode;
            }
        }else{
            graph_name = graph_object.name+"_"+graph_object.dataset_id+"_"+(graph_object.model)
            +"_"+graph_object.explain_id 
            +"_"+(graph_object.graph)+"_"+graph_object.data_type_id+"_SELECTED_"+selectedStr+"_SELECTED_END_"
            +axis_select+"_"+distance_select+"_"+dataSource_select+"_"+color_encode;
        }

        
        
        let graph_in = common.graph_in;
        let graph_target = common.graph_target;
        let graph_out = individual.GCN.graph_out; // 
        let graph_explaination = common.graph_explaination;
        let explaination_type = graph_explaination.type;
        if(explaination_type !== "MessagePassing"){
            console.log("Unsupported explaination type , ", explaination_type);
            return {"success": false};
        }
        if(selectedNodeIdList.length === 0){
            return {"success":false};

        }
        let model_state:any = individual.GCN.model_state_dict;
        let message_passing: any = individual.GCN.message_passing;
        let PathDict = constructPathDict(message_passing);
        let train_mask_set = new Set(common.mask.train);
        function getNodeColorInfo(index:number){
            let ground_truth_label = graph_target.node_features[index];
            //let label = ground_truth_label;
            let GCN_prediction_label = individual.GCN.graph_out.node_features[index];   //
            let MLP_prediction_label = individual.MLP.graph_out.node_features[index]; 
            let GCN_Identity_features_prediction_label = individual.GCN_Identity_features.graph_out.node_features[index]; 
            // Ground Truth Color / Prediction Color
            let color:any = [getCoraNodeColor(ground_truth_label, 2), 
                getCoraNodeColor(GCN_prediction_label,3),
                getCoraNodeColor(MLP_prediction_label,3),
                getCoraNodeColor(GCN_Identity_features_prediction_label,3),
                getTrainColor(index, train_mask_set)
            ];    //
            return color;
        }
        function constructIndentedTree(PathDict:any, select_node:number, level:number, max_level:number, row_id:number, edge_weight:number=0){
            if(level >= max_level){
                return {
                    "name":""+select_node,
                    "children":[],
                    "row_id":row_id,
                    "next_row_id":row_id+1,
                    "edge_weight":edge_weight,
                    "color":getNodeColorInfo(select_node)
                }
            }else{
                let succnodes = Object.keys(PathDict[select_node]);
                let children:any = [];
                let curr_row_id = row_id;
                let next_row_id:number = row_id+1;
                for(let k = 0; k< succnodes.length; k ++ ){
                    let succnode = parseInt(succnodes[k]);
                    let local_edge_weight = PathDict[select_node][succnode];
                    let children_package:any = constructIndentedTree(PathDict, succnode, level+1, max_level, next_row_id, local_edge_weight);
                    next_row_id = children_package.next_row_id;
                    children.push(children_package);
                }
                return {
                    "name":""+select_node,
                    "children":children,
                    "row_id":curr_row_id,
                    "next_row_id":next_row_id,
                    "edge_weight":edge_weight,
                    "color":getNodeColorInfo(select_node)
                }
            }
        }
        function sequentialIndentedTree(indentedTreeNode:any){
            let selectedIndetendedTreeId:any[] = [];
            selectedIndetendedTreeId.push(parseInt(indentedTreeNode["name"]));
            for(let i = 0; i<indentedTreeNode.children.length; i++){
                selectedIndetendedTreeId = selectedIndetendedTreeId.concat(sequentialIndentedTree(indentedTreeNode.children[i]));
            }
            return selectedIndetendedTreeId;
        }
        
        let indentedTree:any = {}
        let selectedIndetendedTreeId:any[] = [];
        if(showSource){
            indentedTree = constructIndentedTree(PathDict, select_inspect_node, 0, extendedMode-1, 0);
            selectedIndetendedTreeId = sequentialIndentedTree(indentedTree);
            //console.log("Selected Indented Tree Id", selectedIndetendedTreeId);
            selectedNodeIdList = selectedIndetendedTreeId;

        }


        let features : any;
        let features_value:any = [];
        let selected_model_weight : any[] = [];
        let selected_model_bias : any[] = [];
        if(dataSource_select === 1){
            features = graph_in.feature;
            features_value = graph_in.feature_value;
            //selected_model_weight = model_state["gc1.weight"];
            //selected_model_bias = model_state["gc1.bias"];
        }else if(dataSource_select === 2){
            features = graph_out.hidden_vector;
            //selected_model_weight = model_state["gc2.weight"];
            //selected_model_bias = model_state["gc2.bias"];
        }else if(dataSource_select === 3){
            features = graph_out.output_vector
        }
        let matrix:any[] = [];
        let selectedFeature:any[] = [];
        let selectedFeatureStatistics:any[] = [];
        let distance_mode = distance_select;
        

        function calculateFeatureDistance(feature1:any, feature2:any){
            let len1 = feature1.length;
            let len2 = feature2.length;
            let common = 0;
            for(let k = 0; k<len1; k++){
                if(feature2.indexOf(feature1[k])>=0){
                    common = common + 1;
                }
            }
            return len1 + len2 - 2* common;
        }
        function calculateEuclideanDistance(feature1:any, feature2:any){
            let len1 = feature1.length;
            //let len2 = feature2.length;
            let dis = 0;
            for(let i = 0; i < len1; i++)
            {
                dis = dis + (feature1[i] - feature2[i]) * (feature1[i] - feature2[i]);
            }
            dis = Math.sqrt(dis);
            return dis;
        }
        let filter_matrix: any[] = [];
        let filter_feature_axis : any[] = [];
        let selectedFilterIdList  : any[] = [];


        

        //let selectNodeList:any[] = [];
        //console.log("Feature Matrix Json:", features, selectedNodeIdList);
        //
        let upper_bound_nodes = 1000;
        if(selectedNodeIdList.length > 0 && selectedNodeIdList.length <= upper_bound_nodes){
            
            if(distance_mode === 2){
                // Calculate distance
                for(let i = 0; i <selectedNodeIdList.length ; i++){
                    let currentSelect:any = selectedNodeIdList[i];
                    //y_axis.push(currentSelect);
                    for(let j = 0;j <selectedNodeIdList.length;j ++){
                        let otherSelect:any = selectedNodeIdList[j];
                        let feature1 = features[currentSelect];
                        let feature2 = features[otherSelect];
                        let distance :any ;
                        if(dataSource_select === 1){
                            distance = calculateFeatureDistance(feature1, feature2);

                        }else{
                            distance = calculateEuclideanDistance(feature1, feature2);
                        }
                        console.log("Distance", distance);
                        let value_package:any;
                        value_package = {
                            "x": i,
                            "y": j,
                            "value": distance
                        }
                        matrix.push(value_package);
                    }
    
                }
                
                
            }else{
                if(dataSource_select === 1){
                    // Input Layer Embedding
                    let pre_selected_feature:any[] = [];
                    let pre_selected_feature_stats:any[] = [];
                    for(let i = 0; i <selectedNodeIdList.length ; i++){
                        let currentSelect:any = selectedNodeIdList[i];
                        //y_axis.push(currentSelect);
                        for(let j = 0;j <features[currentSelect].length;j ++){
                            let dimension = features[currentSelect][j];
                            if(pre_selected_feature.indexOf(dimension) >= 0){
        
                            }else{
                                pre_selected_feature.push(dimension);
                                pre_selected_feature_stats.push(0);
                            }
                            let feature_index = pre_selected_feature.indexOf(dimension);
                            pre_selected_feature_stats[feature_index] = pre_selected_feature_stats[feature_index] + 1;
                        }
                    }
                    //let enableSorting = 1;
                    if(enableSorting){
                        let sort_selected_feature_stats = pre_selected_feature_stats.map((d:any,i:any)=>{
                            return {
                                "index":pre_selected_feature[i],
                                "value":d
                            }
                        })
                        sort_selected_feature_stats.sort((a:any,b:any)=>{
                            return a.value > b.value ? -1: 1;
                        })
                        pre_selected_feature = sort_selected_feature_stats.map((d:any)=>{
                            return d.index;
                        })
                        pre_selected_feature_stats = sort_selected_feature_stats.map((d:any)=>{
                            return d.value;
                        })
                    }
                    selectedFeature = pre_selected_feature;
                    selectedFeatureStatistics = pre_selected_feature_stats;
                    for(let i = 0; i <selectedNodeIdList.length ; i++){
                        let currentSelect:any = selectedNodeIdList[i];
                        //y_axis.push(currentSelect);
                        for(let j = 0;j <features[currentSelect].length;j ++){
                            let dimension = features[currentSelect][j];
                            //if(selectedFeature.indexOf(dimension) >= 0){
        
                            //}else{
                            //    selectedFeature.push(dimension);
                            //    selectedFeatureStatistics.push(0);
                            //}
                            let feature_index = selectedFeature.indexOf(dimension);
                            //selectedFeatureStatistics[feature_index] = selectedFeatureStatistics[feature_index] + 1;
                            let currentNodeIndex = i;
                            let value = features_value[currentSelect][j];
                            let value_package:any;
                            if(axis_select === 1){
                                // nodes in x axis
                                value_package = {
                                    "x": currentNodeIndex,
                                    "y": feature_index,
                                    "value": value
                                }
                            }else{
                                value_package = {
                                    "x": feature_index,
                                    "y": currentNodeIndex,
                                    "value": value
                                }
                            }
                            
                            matrix.push(value_package);
                        }
        
                    }
                    
                    
                }else{
                    for(let i = 0; i <selectedNodeIdList.length ; i++){
                        let currentSelect:any = selectedNodeIdList[i];
                        //y_axis.push(currentSelect);
                        for(let j = 0;j <features[currentSelect].length;j ++){
                            let dimension = j;
                            if(selectedFeature.indexOf(dimension) >= 0){
                                
                            }else{
                                selectedFeature.push(dimension);
                                
                            }
                            let feature_index = j;
                            let currentNodeIndex = i;
                            let value_package:any;
                            let feature_value = features[currentSelect][j];
                            if(axis_select === 1){
                                // nodes in x axis
                                value_package = {
                                    "x": currentNodeIndex,
                                    "y": feature_index,
                                    "value": feature_value
                                }
                            }else{
                                value_package = {
                                    "x": feature_index,
                                    "y": currentNodeIndex,
                                    "value": feature_value
                                }
                            }
                            
                            matrix.push(value_package);
                        }
        
                    }
                }

                // Filter Weight
                if(selected_model_weight.length > 0 && selected_model_bias.length > 0){
                    filter_feature_axis = selectedFeature.slice();
                    filter_feature_axis.push("Bias");
                    for(let j = 0; j< selected_model_weight[0].length; j++){
                        selectedFilterIdList.push(j);
                        for(let i = 0; i<filter_feature_axis.length; i++){
                            let currentFilterIndex = j;
                            let currentFeatureIndex = i;
                            let value : any;
                            if(currentFeatureIndex<filter_feature_axis.length - 1){
                                value = selected_model_weight[i][j];
                            }else{
                                value = selected_model_bias[j];
                            }
                            
                            let value_package:any;
                            if(axis_select === 1){
                                // filters in x axis
                                value_package = {
                                    "x": currentFilterIndex,
                                    "y": currentFeatureIndex,
                                    "value": value
                                }
                            }else{
                                value_package = {
                                    "x": currentFeatureIndex,
                                    "y": currentFilterIndex,
                                    "value": value
                                }
                            }
                            
                            filter_matrix.push(value_package);
                        }
                    
                        
                    }
                }
                
            }
            
        }else{
            return {"success": false}
        }
        //if(selectedFeature.length > 500){
        //    return {"success": false};
         //}
         //console.log("Radial Data PathDict", PathDict)
        /* function getColor(node_id:number){
            let label = graph_target.node_features[node_id];
            if(color_encode === 2){
                label = graph_target.node_features[node_id];
            }else if(color_encode === 3){
                label = graph_out.node_features[node_id];
            }else if(color_encode === 5){
                label = graph_target.node_features[node_id]===graph_out.node_features[node_id];
                
            }
            return getCoraNodeColor(label, color_encode);
        }*/
        let selectedNodeColor : any[] = [];
        for(let i = 0 ; i<selectedNodeIdList.length ; i++){
            let color = getNodeColorInfo(selectedNodeIdList[i]);
            selectedNodeColor.push(color);
        }
        
        
        
        let data_type = common.data_type_id;
        if(data_type === 2){
            let graph_info = common.graph_additional_info;
            let selectedFeatureLabel:any = [];
            
            if(Object.keys(graph_info).indexOf("idx_to_attr")>=0 && dataSource_select === 1){
                let idx_to_attr = graph_info.idx_to_attr;
                for(let i=0;i<selectedFeature.length; i++){
                    selectedFeatureLabel.push(idx_to_attr[selectedFeature[i]]);
                }
            }else{
                selectedFeatureLabel = selectedFeature;
            }




            // Filter Graph Json - ------------------------------------------------------
            let filter_graph_json:any = {};
            if(distance_mode === 1){
                if(dataSource_select === 1 || dataSource_select === 2){
                    let filter_x_axis_data : any[] = [];
                    let filter_y_axis_data: any[] = [];
                    let filter_type = "continuous";
                    if(axis_select === 1){
                        filter_x_axis_data = selectedFilterIdList;
                        filter_y_axis_data = filter_feature_axis;
                    }else{
                        filter_x_axis_data = filter_feature_axis;
                        filter_y_axis_data = selectedFilterIdList;
                    }
                    let filter_color_info: any = {
                        "enable_x_axis_color": false,
                        "enable_y_axis_color": false,
                        "x_axis_color" : [],
                        "y_axis_color" : []
                    }
                
                    filter_graph_json = {
                        "success": true,
                        "name": graph_name,
                        "matrix": filter_matrix,
                        "x_axis": filter_x_axis_data,
                        "y_axis": filter_y_axis_data,
                        "distance_select": distance_select,
                        "type": filter_type,
                        "color_info": filter_color_info
                    }
                }else{
                    filter_graph_json = {
                        "success": false
                    }
                }
            }else{
                filter_graph_json = {
                    "success": false
                }
            }
            // Graph Json ------------------------------------------------------------
            let type : any;
            if(dataSource_select === 1){
                type = "discrete";
            }else{
                type = "continuous";
            }
            
            let graph_json:any, x_axis_data : any, y_axis_data : any;
            let enable_x_axis_color : boolean = false;
            let enable_y_axis_color : boolean = false;
            let x_axis_color:any[] = [];
            let y_axis_color:any[] = [];
            if(distance_mode === 2){
                x_axis_data = selectedNodeIdList;
                y_axis_data = selectedNodeIdList;
                enable_x_axis_color = true;
                enable_y_axis_color = true;
                x_axis_color = selectedNodeColor;
                y_axis_color = selectedNodeColor;
            }else{
                if(axis_select === 1){
                    x_axis_data = selectedNodeIdList;
                    y_axis_data = selectedFeatureLabel;
                    enable_x_axis_color = true;
                    enable_y_axis_color = false;
                    x_axis_color = selectedNodeColor;
                    y_axis_color = [];
                }else{
                    x_axis_data = selectedFeatureLabel;
                    y_axis_data = selectedNodeIdList;
                    enable_x_axis_color = false;
                    enable_y_axis_color = true;
                    x_axis_color = [];
                    y_axis_color = selectedNodeColor;
                }
            }
            let color_info : any = {}
            color_info["enable_x_axis_color"] = enable_x_axis_color;
            color_info["enable_y_axis_color"] = enable_y_axis_color;
            color_info["x_axis_color"] = x_axis_color;
            color_info["y_axis_color"] = y_axis_color;
            graph_json = {
                "success":true,
                "name":graph_name,
                "matrix":matrix,
                "x_axis":x_axis_data,
                "y_axis":y_axis_data,
                "distance_select": distance_select,
                "type" : type,
                "color_info": color_info,
                "selectedFeatureStatistics":selectedFeatureStatistics,
                "indentedTree": indentedTree,
                "filter_graph_json": filter_graph_json,
                "showSource":showSource
            }
            
            return graph_json;
        }else{
            console.log("Unknown data type : ", data_type )
            return {"success":false}; 
        }


        
    }
    public onAxisChange(axis_select: number) {
        this.setState({
            axis_select:axis_select
        })
    }
    public onDistanceChange(distance_select: number) {
        this.setState({
            distance_select:distance_select
        })
    }
    public onDataSourceChange(dataSource_select: number){
        this.setState({
            dataSource_select: dataSource_select
        })
    }
    public onColorEncodeChange(color_encode: number){
        this.setState({
            color_encode: color_encode
        })
    }
    public onEnableSort(enableSorting: boolean){
        this.setState({
            enableSorting: enableSorting
        })
    }
    public render() {
        let {graph_object, specificNodeIdList} = this.props;
        let common;
        if(getLayoutMode()===3){
            common = graph_object.common;
        }else{
            common = graph_object;
        }
        let explanation_type = common.graph_explaination.type;
        let feature_matrix_json = this.constructFeatureMatrixJson(graph_object, specificNodeIdList,
             this.state.axis_select, this.state.distance_select, this.state.dataSource_select, this.state.color_encode,
            this.props.select_inspect_node, this.props.showSource, this.state.enableSorting, this.props.width, this.props.height, this.props.extendedMode);
        //let filter_graph_json = feature_matrix_json["filter_graph_json"];
        //console.log("Filter_Graph_JSON",filter_graph_json)
        //console.log("Feature Matrix Json", feature_matrix_json);
        if(explanation_type == "MessagePassing" && feature_matrix_json["success"]){
            /*
            let dataSourceOptions = [
                [1, "Input Layer"],
                [2, "Hidden Layer"],
                [3, "Output Layer"]
            ];
            let distanceOptions = [
                [1, "Detailed Feature"],
                [2, "Distance"]
            ];

            let axisOptions = [
                [1, "X Axis"],
                [2, "Y Axis"]
            ];
            let colorOptions = [
                [2, "Ground Truth"],
                [3, "Model Output"],
                [5, "True / False"]
            ]
            let generateSelect = (options:any[], select_num:number, onChange:any, tips:any) =>{
                return (<Select
                        placeholder={tips}
                        value={select_num}
                        style={{ width: '200px' }}
                        onChange={onChange}
                    >
                        { options.map((value:any)=>(
                                <Option key={value[0]} value={value[0]}>{value[1]}</Option>
                        ))}
                        
                    </Select>);
            }*/
            let indentedtreeWidth = 120;
            if(!this.props.showSource){
                indentedtreeWidth = 60;
            }
            let matrixWidth = this.props.width-indentedtreeWidth;
            let matrixRealWidth = matrixWidth - 30;
            let max_row_num_block = Math.max(1, Math.floor((matrixRealWidth) / 15));
            let gridSize = Math.floor((matrixRealWidth) / (max_row_num_block));
            let indentedtreeBarHeight = gridSize;
            let FeatureMatrixHeight = 180 + feature_matrix_json.y_axis.length * indentedtreeBarHeight + 20;
            let BrushBarChartLayoutConfig = {
                "width":this.props.width-indentedtreeWidth,
                "height":100,
                "x":indentedtreeWidth,
                "y":0,
                "max_row_num_block":max_row_num_block
            }
            let MatrixLayoutConfig = {
                "width":this.props.width-indentedtreeWidth,
                "height":FeatureMatrixHeight-180,
                "x":indentedtreeWidth,
                "y":180,
                "gridSize":gridSize,
                "max_row_num_block":max_row_num_block
            }
            let IndentedTreeLayoutConfig = {
                "width":indentedtreeWidth,
                "height":FeatureMatrixHeight-180,
                "x":0,
                "y":180,
                "barHeight":indentedtreeBarHeight
            }
            let disableSort = () =>{
                this.onEnableSort(false);
            }
            let enableSort = () =>{
                this.onEnableSort(true);
            }
            let onChangeSort = (e:any) =>{
                console.log(e);
                this.onEnableSort(e);
            }
            //const [top, setTop] = useState(10);
            return (            
            <div >
                <div className="ViewTitle">Feature Matrix View
                <div style={{float:'right'}}>
                    
                    
                    
                    </div>
                
                
                </div>
                <div className="ViewBox" style={{width: "100%", height:""+(this.props.height - 40)+"px", overflowX: "scroll"}}>
                    
                    <div
                    style={{
                        
                        width: '100%',
                        }}
                    >
                    {/*Data Source :
                    {generateSelect(dataSourceOptions, this.state.dataSource_select, this.onDataSourceChange, "Select the displayed data source.")}
                    Display info in :
                    {generateSelect(distanceOptions,this.state.distance_select,this.onDistanceChange,"Select show which mode the matrix display in")}
                    Show nodes in :
                    {generateSelect(axisOptions,this.state.axis_select,this.onAxisChange, "Select a nodes in which axis")}
                    Color encode: 
                    {generateSelect(colorOptions,this.state.color_encode, this.onColorEncodeChange, "Select color encode")}*/}
                     {/*<WordCloud id={5} feature_matrix_json={feature_matrix_json} width={this.props.width} height={this.props.height}  />*/}
                     {/*(filter_graph_json["success"])?(<Matrix id={2} feature_matrix_json={filter_graph_json} 
                            width={this.props.width} height={this.props.height}  />):(<div />)*/}
                        <div
                        style={{
                            
                            width: '100%',
                            height: ""+FeatureMatrixHeight+"px"
                            }}
                        >
                        
                            <svg
                                        style={{ height: "100%", width:  "100%"}}
                                        id="FeatureMatrixSVGChart"
                                        xmlns="http://www.w3.org/2000/svg"
                            >
                                            
                                        
                                            <foreignObject x={BrushBarChartLayoutConfig.x - 50} y="20" width="50" height="40">
                                                
                                                <div>
                                                {/*(this.state.enableSorting)?
                        <Button type="primary" size="small" onClick={disableSort}>Sort by node order</Button>:
                                                <Button type="default" size="small" onClick={enableSort}>Sort by frequency of features</Button>*/}
                                                <Switch checked={this.state.enableSorting} onChange={onChangeSort} />
                                                </div>
                                            </foreignObject>
                           
                            <BrushBarChartContainer id={3} feature_matrix_json={feature_matrix_json} layout_config={BrushBarChartLayoutConfig}   />
                            
                            <MatrixContainer id={1} feature_matrix_json={feature_matrix_json} layout_config={MatrixLayoutConfig}   />
                            
                            
                            {(this.props.showSource)?(<IndentedTreeContainer id={4} feature_matrix_json={feature_matrix_json} layout_config={IndentedTreeLayoutConfig}   />):<g />}
                            </svg>
                        
                        </div>
                    </div>
                </div>
            </div>)
        }else{
            return <div style={{width: "100%", height:""+(this.props.height - 20)+"px", overflowX: "scroll"}}>
            <div className="ViewTitle">Feature Matrix View
            
                </div></div>
               // return <div>Please choose message passing explanation method. Please choose [1,100] nodes in graph view. Aggregated feature dimension should be no more than 500.</div>
        }
        //return <div>Message Passing View</div>
    //}
    }
}

