    public test(){
        //console.log(point_array);
        const width:number = 300,
                height:number = 300,
                margin = 40,
                scalepop = d3.scaleSqrt().domain([0, 100000]).range([0.2, 24]),
                scalecountry = d3.scaleOrdinal(d3.schemeCategory20b),
                centerx = d3.scaleLinear().range([width / 2 - height / 2 + margin, width / 2 + height / 2 - margin]),
                centery = d3.scaleLinear().range([margin, height - margin]);

        d3.csv('cities.csv', function (cities:any) {

            const data = cities
                .sort((a:any, b:any) => d3.descending(+a[2015], +b[2015]))
                .map((d:any, i:any) => [+d.Longitude, +d.Latitude, d['Urban Agglomeration'], +d[2015], +d['Country Code']])
                .slice(0, 800);

            const canvas = d3.select("#chart").append("canvas")
                .attr("width", width)
                .attr("height", height);

            const model = new tsnejs.tSNE({
                dim: 2,
                perplexity: 30,
            });

            // initialize data with pairwise distances
            const dists = data.map((d:any) => data.map((e:any) => d3.geoDistance(d, e)));
            model.initDataDist(dists);


            const forcetsne = d3.forceSimulation(
            data.map((d:any) => (d.x = width / 2, d.y = height / 2, d))
            )
                .alphaDecay(0.005)
                .alpha(0.1)
                .force('tsne', function (alpha:any) {
                    // every time you call this, solution gets better
                    model.step();

                    // Y is an array of 2-D points that you can plot
                    let pos = model.getSolution();

                    centerx.domain(d3.extent(pos.map((d:any) => d[0])));
                    centery.domain(d3.extent(pos.map((d:any) => d[1])));

                    data.forEach((d:any, i:any) => {
                        d.x += alpha * (centerx(pos[i][0]) - d.x);
                        d.y += alpha * (centery(pos[i][1]) - d.y);
                    });
                })
                .force('collide', d3.forceCollide().radius((d:any) => 1.5 + scalepop(d[3])))
                .on('tick', function () {

                    let nodes = data.map((d:any, i:any) => {
                        return {
                            x: d.x,
                            y: d.y,
                            r: scalepop(d[3]),
                            color: scalecountry(d[4]),
                        };
                    });

                    draw(canvas, nodes);

                });

            function draw(canvas:any, nodes:any) {
                let context = canvas.node().getContext("2d");
                context.clearRect(0, 0, width, width);

                for (var i = 0, n = nodes.length; i < n; ++i) {
                    var node = nodes[i];
                    context.beginPath();
                    context.moveTo(node.x, node.y);
                    context.arc(node.x, node.y, node.r, 0, 2 * Math.PI);
                    context.lineWidth = 0.5;
                    context.fillStyle = node.color;
                    context.fill();
                }
            }

        });

    }
